import React, { useContext } from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import { ThemeProvider } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Context from "../../components/App/Context";
import useStyles from "./styles";
import HomeEl from '../../components/Home/el'
import ProimiakiEl from '../../components/Proimiaki/el'
import OrthrosEl from '../../components/Orthros/el'
import Ora1El from '../../components/Ora1/el'
import Ora3El from '../../components/Ora3/el'
import Ora6El from '../../components/Ora6/el'
import Ora9El from '../../components/Ora9/el'
import EsperinosEl from '../../components/Esperinos/el'
import MetalipseosEl from '../../components/Metalipseos/el'
import ApodeipnoEl from '../../components/Apodeipno/el'
import PsaltirionEl from '../../components/Psaltirion/el'

const Container = ({ comp }: any) => {
  const classes = useStyles();
  const context = useContext(Context);

  return (
    <div className={classes.root}>
    <CssBaseline />
    <ThemeProvider theme={context.theme}>
      <Paper 
        className={classes.content}
        style={{
          fontSize: context.state.fontSize,
        }}
      >
        {console.log(context.theme)}
        {comp==='home' && <HomeEl />}
        {comp==='proimiaki' && <ProimiakiEl red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled}/>}
        {comp==='orthros' && <OrthrosEl red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
        {comp==='ora1' && <Ora1El red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
        {comp==='ora3' && <Ora3El red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
        {comp==='ora6' && <Ora6El red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
        {comp==='ora9' && <Ora9El red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
        {comp==='esperinos' && <EsperinosEl red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
        {comp==='metalipseos' && <MetalipseosEl red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
        {comp==='apodeipno' && <ApodeipnoEl red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
        {comp==='psaltirion' && <PsaltirionEl red={context.theme.palette.error.main} grey={context.theme.palette.text.disabled} />}
      </Paper>
      </ThemeProvider>
    </div>
  );
};

export default Container;
