import React from "react";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Typography from "@material-ui/core/Typography";
import FormControl from "@material-ui/core/FormControl";
import ColorLensIcon from "@material-ui/icons/ColorLens";
import FontDownloadIcon from "@material-ui/icons/FontDownload";
import GTranslateIcon from "@material-ui/icons/GTranslate";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import ToggleButton from "@material-ui/lab/ToggleButton";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";
import { ButtonGroup } from "@material-ui/core";
import Divider from '@material-ui/core/Divider';
import { DrawerProps } from "./types";
import { Link } from "@material-ui/core";
import locale from "../../locale";

const Drawer = ({
  classes,
  handleChangeContext,
  context,
  handleChangeLocale,
}: DrawerProps) => (
  <List>
    <div className={classes.drawerSpacer}/>
    <ListItem button key="home">
      {/* <FormControl className={classes.formControl}> */}
        <Link href="#/">
          {locale[context.state.locale].home}
        </Link>
      {/* </FormControl> */}
    </ListItem>

    <ListItem button key="proimiaki">
      <Link href="#/proimiaki" >
        {locale[context.state.locale].proimiaki}
      </Link>
    </ListItem>

    <ListItem button key="orthros">
      <Link href="#/orthros" >
        {locale[context.state.locale].orthros}
      </Link>
    </ListItem>

    <ListItem button key="ora1">
      <Link href="#/ora1" >
        {locale[context.state.locale].ora1}
      </Link>
    </ListItem>

    <ListItem button key="ora3">
      <Link href="#/ora3" >
        {locale[context.state.locale].ora3}
      </Link>
    </ListItem>

    <ListItem button key="ora6">
      <Link href="#/ora6" >
        {locale[context.state.locale].ora6}
      </Link>
    </ListItem>

    <ListItem button key="ora9">
      <Link href="#/ora9" >
        {locale[context.state.locale].ora9}
      </Link>
    </ListItem>

    <ListItem button key="esperinos">
      <Link href="#/esperinos" >
        {locale[context.state.locale].esperinos}
      </Link>
    </ListItem>

    <ListItem button key="metalipseos">
      <Link href="#/metalipseos" >
        {locale[context.state.locale].metalipseos}
      </Link>
    </ListItem>

    <ListItem button key="apodeipno">
      <Link href="#/apodeipno" >
        {locale[context.state.locale].apodeipno}
      </Link>
    </ListItem>

    <ListItem button key="psaltirion">
      <Link href="#/psaltirion" >
        {locale[context.state.locale].psaltirion}
      </Link>
    </ListItem>

    <Divider />

    <ListItem key="theme">
      <ListItemIcon>
        <ColorLensIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <ButtonGroup>
          <Button
            aria-label="light"
            className={classes.buttonThemeLight}
            name="theme"
            value="0"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
          <Button
            aria-label="tan"
            className={classes.buttonThemeTan}
            name="theme"
            value="1"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
          <Button
            aria-label="dark"
            className={classes.buttonThemeDark}
            name="theme"
            value="2"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
        </ButtonGroup>
      </FormControl>
    </ListItem>

    <ListItem key="font">
      <ListItemIcon>
        <FontDownloadIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
          // className={classes.navigation}
        >
          <Grid item>
            <Button
              variant="outlined"
              color="default"
              size="small"
              name="fontSize"
              className={classes.buttonFont}
              value={context.state.fontSize - 1}
              onClick={handleChangeContext}
            >
              -
            </Button>
          </Grid>
          <Grid item>
            <Typography>{context.state.fontSize}px</Typography>
          </Grid>
          <Grid item>
            <Button
              variant="outlined"
              color="default"
              size="small"
              name="fontSize"
              value={context.state.fontSize + 1}
              className={classes.buttonFont}
              onClick={handleChangeContext}
            >
              +
            </Button>
          </Grid>
        </Grid>
      </FormControl>
    </ListItem>

    <ListItem key="locale">
      <ListItemIcon>
        <GTranslateIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <ToggleButtonGroup
          value={context.state.locale}
          exclusive
          aria-label="Locale"
        >
          <ToggleButton
            value="el"
            aria-label="ellinika"
            onClick={() => handleChangeLocale("el")}
          >
            <img src="assets/el.gif" alt="el" />
          </ToggleButton>
          <ToggleButton
            value="en"
            aria-label="english"
            onClick={() => handleChangeLocale("en")}
          >
            <img src="assets/en.gif" alt="el" />
          </ToggleButton>
        </ToggleButtonGroup>
      </FormControl>
    </ListItem>


  </List>
);

export default Drawer;
